.btnExpandFormat{
  cursor:pointer;
  background: #85a5ff;
  border: none;
  text-align: left;
  width: 600px;
  font-size: large;
  padding-bottom: 15px;
}

.btnExpandFormat:hover {
  background: #597ef7;
  opacity: 0.5;
  transition: background ease 0.2s;
}

.topbar{
  background: #2f54eb;
  width: 600px;
}

.open {
  transition: height ease 0.5s;
}

.openCenarios {
  height: 100%;
  transition: height ease 0.5s;
}

.content {
  height: 0px;
  overflow: hidden;
  transition: height ease 0.0s;
}

